<template>
  <div class="about">
    <el-row v-loading="loading">
      <el-row>
        <h2 class="text-center pt-10">先来一碗毒鸡汤</h2>
        <p class="font-size--medium p-15 jitang" style="width: 50%; margin: 0 auto;">
          {{dailyInfo.wxs}}
        </p>
      </el-row>
      <h2 class="text-center">新闻速递</h2>
      <el-row class="text-left ptb-20 news_content" v-for="(item, index) in dailyInfo.news" :key="index">
        <el-row class="ptb-10 text-center">{{item.news_title}}</el-row>
        <el-row class="text-center">
          <el-image :src="item.news_img"
                    v-viewer="{toolbar:false, title: false, navbar: false, keyboard: false}"></el-image>
        </el-row>
        <el-row class="text-left ptb-15" style="width: 50%; line-height: 32px; margin: 0 auto;">{{item.news_content}}</el-row>
        <el-row type="flex" justify="center">
          <el-link :href="item.news_url" target="_blank" type="primary">去看看</el-link>
          <el-row class="ml-15">{{item.news_time}}</el-row>
        </el-row>
      </el-row>
    </el-row>
  </div>
</template>
<script>
import { getDailyLook } from '@/api/apiopen'

export default {
  name: 'about',
  data () {
    return {
      dailyInfo: {},
      loading: false
    }
  },
  created () {
    this.getDailyInfo()
  },
  methods: {
    getDailyInfo () {
      this.loading = true
      getDailyLook({
        param: {}
      }).then(res => {
        this.loading = false
        this.dailyInfo = res
      }).catch(err => {
        this.loading = false
        this.$message.error('每日一刊获取失败')
        console.log(err)
      })
    },
    // 上下拉刷新
    init () { //兼容pc
      let _this = this
      window.onmousewheel = function (e) {
        if (e.wheelDelta < 0) {// 固定值120 向上滚为-120 向下是120
          _this.$refs.newsScroller && (_this.$refs.newsScroller.scrollBy(0, 30, false))//避免ref中存在scrollerBottom
        } else {
          _this.$refs.newsScroller && (_this.$refs.newsScroller.scrollBy(0, -30, false))
        }
      }
    },
    refresh () {
      console.log('刷新')
      let that = this
      setTimeout(function () {
        that.$refs.newsScroller.finishPullToRefresh()
      }, 3000)
    },
    infinite () {
      console.log('加载')
    },
  }
}
</script>
<style lang="scss">
.about {

  h2 {
    padding: 0;
    margin: 0;
  }

  .jitang {
    background-image: linear-gradient(30deg, #29bdd9, #276ace);
  }

  .news_content {
    background-image: linear-gradient(180deg, #ff9569 0%, #e92758 100%);
  }

  .el-image {
    width: 500px;
    text-align: center;

    img {
      width: 100%;
    }
  }

  .el-link {
    cursor: pointer;
  }

  p {
    text-indent: 2em;
  }
}
</style>
